import editorjsHTML from 'editorjs-html';
import React from 'react';
import EditorJs from 'react-editor-js';
import MultipleValueTextInput from 'react-multivalue-text-input';
import { alertBox, switchLoader } from '../../../commonRedux/';
import Button from '../../../components/Button';
import Spinner from '../../../components/Spinner';
import { createGig, getCategories, getGig, getGigDraft } from '../../../http/gig-calls';
import { EDITOR_JS_TOOLS } from '../../MyBlogs/tools';


require ("./styles.scss");
let coins = require('./coins.json');

class AddGigTab extends React.Component {
  constructor(props) {
    super(props);
    const edjsParser = editorjsHTML();
      this.state = {
        id:0,
        ide: false, 
        edjsParser: edjsParser,
        tags: [],
        data:{},
        level: 1,
        dragclass: '',
        parent: "",
        files: [],
        files2: [],
        features: [{feature: '', standard:0, premium: 1}],
        extras: [{feature: '', value: '', amount: ''}],
        level1: [],
        level2: [],
        level3: [],
        level4: [],
        level5: [],
        coins : coins,
        title: '',
        currency: "",
        category: "",
        category_id: "",
        sub_category: "",
        sub_category_id: "",
        format: "",
        standard_days: "",
        premium_days: "",
        standard_price: "",
        premium_price: "",
        fast_days: "",
        fast_price: "",
        desc: "",
        gallery:[],
        loading:true
      };
    
  
      this.editor = React.createRef();
      this.reactTags = React.createRef()
  }

  

  onDelete (i) {
    const tags = this.state.tags.slice(0)
    tags.splice(i, 1)
    this.setState({ tags })
  }
 
  onAddition (tag) {
    const tags = [].concat(this.state.tags, tag)
    this.setState({ tags })
  }

  tagChange = (val) => {
    console.log(val)
  }


  handleFeature = (i, e) => {
   
    var name =  e.target.name;
    var temp = this.state.features;
    if(name == 'feature'){
      var val =  e.target.value;
      temp[i][name] = val;
    }else{
      var val =  e.target.checked;
      temp[i][name] = val ? 1 : 0;
    }
    console.log(temp);
    this.setState({
        features : temp
    })
  };

  addFeature = () => {
    var temp = this.state.features;
    temp.push({feature: '', standard: 0, premium : 1});
    this.setState({
      features: temp
    })
  }

  removeFeature = (i) => {
    var temp = this.state.features;
    delete temp[i];
    this.setState({
      features: temp
    })
  }

  handleExtra = (i, e) => {
   
    var name =  e.target.name;
    var temp = this.state.extras;
    var val =  e.target.value;
    temp[i][name] = val;
    console.log(temp);
    this.setState({
        extras : temp
    })
  };

  addExtra = () => {
    var temp = this.state.extras;
    temp.push({feature: '', value: '', amount : ''});
    this.setState({
      extras: temp
    })
  }

  removeExtra = (i) => {
    var temp = this.state.extras;
    delete temp[i];
    this.setState({
      extras: temp
    })
  }


  componentDidMount() {    
    if(this.props.id != undefined && this.props.id != null){
      this.getData();
    }else{
      this.setState({
        loading:false
      })
    }
     this.getGigCategories(1);
  }

  componentDidUpdate(prevProps) {
    console.log(this.props.id)
    if(this.props.id != prevProps.id && this.props.id != undefined && this.props.id != null){
      this.getData();
    }
  }

  getData = () => { 
    getGig({id: this.props.id}).then(resp=>{
      try {
        var edc = JSON.parse(resp.editorContent);
      console.log(edc);
      this.setState({
        
          title: resp.subject,
          format: resp.format,
          currency: resp.preferedCurrency,
          // desc: resp.text,
          data: edc,
          tags: resp.hashtags == undefined || resp.hashtags[0] == undefined || resp.hashtags[0] == '' ? [] : resp.hashtags,
          extras: resp.extras,
          standard_days: resp.standardDays,
          premium_days: resp.premiumDays,
          standard_price: resp.standardPrice,
          premium_price: resp.premiumPrice,
          fast_days: resp.fastDays,
          fast_price: resp.fastPrice,
          features: resp.features,
          category: resp.category,
          category_id: resp.categoryId,
          sub_category: resp.subCategory,
          sub_category_id: resp.subCategoryId,
          files2: resp.contents
      } , () => {
        setTimeout(() => {
          this.setState({
          loading:false,
          })
        }, 1000);
      })
      } catch (error) {
        this.setState({
          ide:true,
          loading:false,
          })
      }
      
     }, err=>{
        this.setState({
          loading:false,
          })
     });
  }


  getDraft = () => { 
    getGigDraft().then(resp=>{
      var edc = JSON.parse(resp.editorContent);
      console.log(edc);
      this.setState({
        id: resp._id,
          title: resp.subject,
          format: resp.format,
          currency: resp.preferedCurrency,
          // desc: resp.text,
          data: edc,
          tags: resp.hashtags == undefined || resp.hashtags[0] == undefined || resp.hashtags[0] == '' ? [] : resp.hashtags,
          extras: resp.extras,
          standard_days: resp.standardDays,
          premium_days: resp.premiumDays,
          standard_price: resp.standardPrice,
          premium_price: resp.premiumPrice,
          fast_days: resp.fastDays,
          fast_price: resp.fastPrice,
          features: resp.features,
          category: resp.category,
          category_id: resp.categoryId,
          sub_category: resp.subCategory,
          sub_category_id: resp.subCategoryId,
          files2: resp.contents
      } , () => {
        setTimeout(() => {
          this.setState({
          loading:false,
          })
        }, 1000);
      })
     }, err=>{
      this.setState({
        loading:false,
        })
     });
  }

  getGigCategories = (level) => {
    getCategories({level: level, parent: this.state.parent}).then(resp => {
      this.setState({
        ["level"+level] : resp
      })
    }, err => {
      console.log(err);
    })
  }
  

  handleInput = (e) => {
    var val =  e.target.value;
    var name =  e.target.name;
    if(name == 'title'){
      console.log(val.length);
      if(val.length > 100){
        console.log("over")
        val = val.substring(0, 100);
        console.log(this.state.title);
        console.log(val);
      }
      this.setState({
          [name] : val
      })
    }else{
      this.setState({
        [name] : val
      })
    }
  };

  handleCategory = (val, i) => {
      
      var v = val.split("^");
      if(i == 2){
        this.setState({
          category: v[1],
          category_id: v[0],
          parent: v[0],
          level:i
        }, () => {
          if(i < 6){
          this.getGigCategories(i);
          }
        });
      }else{
        this.setState({
          sub_category: v[1],
          sub_category_id: v[0],
          parent: v[0],
          level:i
        }, () => {
          if(i < 6){
          this.getGigCategories(i);
          }
        });
      }

    
  };


  handleFile = (name, type, value) => {
    console.log(value);
    this.setState({
        [name] : value
    },()=>{
      var reader  = new FileReader();
    // it's onload event and you forgot (parameters)
      reader.onload = function(e)  {
          var image = document.getElementById(name+"_preview");
          image.src = e.target.result;
      }
      // you have to declare the file loading
      reader.readAsDataURL(value);
    })
  };
  

  submit = async (e, t) => {
    e.preventDefault();
    var edata = await this.editor.save();
    var status = 'active';
    var err = [];
    if(t == 1){
    if(this.state.title == ''){
      err.push("Title is required");
    }
    if(this.state.category == ''){
      err.push("Category is required");
    }
    if(edata.blocks.length <= 0 ){
      err.push("About content is required");
    }
    if(this.state.files.length == 0 && this.state.files2.length == 0){
      err.push("At least one gallery image is required");
    }
  }else{
    if(this.state.title == ''){
      err.push("Title is required");
    }
    status = 'draft';
  }

    if(err.length > 0){
      alertBox(true, err.join(", "));
    }
    else {
      console.log(edata);
      var data = this.state.edjsParser.parse(edata);
      data = data.join(" ");
      
      if(this.state.tags.length > 0){
        var hashtags = this.state.tags.join(",");
      }else{
        var hashtags = '';
      }
      
      var formData = {
        status: status,
        subject: this.state.title,
        category: this.state.category,
        category_id: this.state.category_id,
        sub_category: this.state.sub_category,
        sub_category_id: this.state.sub_category_id,
        format: this.state.format,
        currency: this.state.currency,
        standard_price: this.state.standard_price,
        premium_price: this.state.premium_price,
        standard_days: this.state.standard_days,
        premium_days: this.state.premium_days,
        features: JSON.stringify(this.state.features),
        extras: JSON.stringify(this.state.extras),
        fast_days: this.state.fast_days,
        fast_price: this.state.fast_price,
        hashtags: hashtags,
        text: data,
        editorContent: JSON.stringify(edata),
        files: this.state.files,
        files2: this.state.files2
      };
      if(this.props.id != undefined && this.props.id != null && !this.state.ide){
        formData.id = this.props.id;
      }
      if(this.state.id != 0 && !this.state.ide){
        formData.id = this.state.id;
      }

      console.log(formData);
     
      switchLoader('Your post is being updated...');
      createGig(formData)
      .then(async resp => {
        if(t==1){
          window.location.href="/passionomy/dashboard";
          //this.props.history.push("/passionomy/seller")
        }else{
          this.props.history.push("/passionomy/gig/edit/"+resp.post._id);
          alertBox(true, "Draft has been saved!", "success");
        }
    
        switchLoader();
      }, error => {
        alertBox(true, error.message);
        switchLoader();
      });
    }
  }
  

  handleDragEnter = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: 'enter'
    })
  };
  handleDragLeave = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
  };
  handleDragOver = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: 'over'
    })
  };
  handleDrop = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
    let dt = e.dataTransfer
    let files = dt.files
    this.handleFiles(files);
  };
  addToGallery = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
    console.log(e.target.files);
    this.handleFiles(e.target.files);
  };
  
  handleClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    var el = document.querySelector(".droppedFiles");
    if(!el.contains(e.target)){
    document.getElementById('gallery').click();
    }
  };

  fileToDataURL = (file) => {
    var reader = new FileReader()
    return new Promise(function (resolve, reject) {
      reader.onload = function (event) {
        resolve(event.target.result)
      }
      reader.readAsDataURL(file)
    })
  }  
  
  readAsDataURL = (filesArray) => {
    // target => <input type="file" id="file">
    // console.log(target);
    // var filesArray = Array.prototype.slice.call(target.files)      
    return Promise.all(filesArray.map(this.fileToDataURL))
  }
  handleFiles = async (files) => {
  
    var temp = [...this.state.files, ...files];
    console.log(temp);
    var f = await this.readAsDataURL(temp);
    console.log(f);
    this.setState({
      files:  temp,
      imgs: f
    })
  }

  removeFile = (i, n=1) => {
    if(n==2){
      var temp = this.state.files2;
      temp.splice(i, 1);
      this.setState({
        files2:temp,
      })
    }else{
    var temp = this.state.files;
    var temp2 = this.state.imgs;
    temp.splice(i, 1);
    temp2.splice(i,1);
    this.setState({
      files:temp,
      imgs:temp2
    })
  }
  }


    render() {
    
        return (                  
                 <div>
                  
                   <form onSubmit={(e) => this.submit(e, 1)} method="post">
                      <div className="form-group clearfix">
                      <div><strong>Gig Title</strong></div>
                      <input type="text" required placeholder="I will help you for..." name="title" className="form-control w-100" onChange={this.handleInput} value={this.state.title} />
                                <span className="add-comment pull-right">
                                    {this.state.title.length} / 100 max
                                </span>
                      </div>
                      <div className="row">
                        <div className="col-md-6">
                          <div className="form-group">
                            <div className="left">
                              <strong>Category</strong>
                              </div>
                              <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e.target.value, 2)} >
                                  <option value="">Select</option>
                                  {
                                    this.state.level1.map((c, k) => {
                                      return <option key={k} value={c._id+"^"+c.category} selected={this.state.category == c.category}>{c.category}</option>
                                    })
                                  }
                                </select>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-group">
                              <div className="left">
                                <strong>Sub Category</strong>
                                </div>
                                
                                  <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e.target.value, 3)} >
                                  <option value="">Select</option>
                                  {
                                    this.state.level2.length > 0 && this.state.level2.map((c, k) => {
                                      return <option key={k} value={c._id+"^"+c.category} selected={this.state.sub_category == c.category}>{c.category}</option>
                                    })
                                  }
                                </select>
                            </div>
                        </div>
                      </div>

                      <div className="row">
                        <div className="col-md-6">
                          <div className="form-group">
                            <div className="left">
                              <strong>File Format</strong>
                              </div>
                              <input type="text"  requiredplaceholder="Enter all file formats you will be sending" name="format" className="form-control w-100" value={this.state.format} onChange={this.handleInput} />
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-group">
                              <div className="left">
                                <strong>Preferred Currency</strong>
                                </div>
                                <select name="currency" required className="form-control w-100" onChange={this.handleInput} >
                                  <option value="">Select</option>
                                  {
                                    this.state.coins.map((e, i) => {
                                      return <option value={e.currencySymbol} selected={e.currencySymbol == this.state.currency}>{e.currencyName+" ("+e.currencySymbol+")"}</option>
                                    })
                                  }
                                </select>
                            </div>
                        </div>
                      </div>

                      <div className="form-group">
                   <div className="left">
                              <strong>About</strong>
                              </div>
                              <div className="right">
                              {
                     this.state.loading ? 
                     <Spinner />
                     : <EditorJs instanceRef={instance => {this.editor = instance}} data={this.state.data} tools={EDITOR_JS_TOOLS} /> }
{/* 
                                  <textarea style={{display:'none'}}
                                    disabled
                                    value={draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))}
                                  /> */}
                                {/* <textarea type="text" required placeholder="Description about this gig" name="desc" className="form-control w-100" onChange={this.handleInput} >{this.state.desc}</textarea> */}
                              </div>
                   </div>
                      
                      <div className="form-group">
                                  <div><strong>Pricing</strong></div>
                                  <div className="right">
                                <table className="table table-bordered">
                                  <tr><th></th><th>Standard</th><th>Premium</th></tr>
                                  <tr><td>Price</td><td><span className="input-icon">{this.state.currency}</span><input type="number" required placeholder="" step="1" min = "1" name="standard_price" className="form-control w-100" value={this.state.standard_price} onChange={this.handleInput} required /></td><td><span className="input-icon">{this.state.currency}</span> <input type="number" required placeholder="" step="1" min = "1" name="premium_price" className="form-control w-100" onChange={this.handleInput} required  value={this.state.premium_price} /></td></tr>
                                  <tr><td>Delivery Time</td><td><input type="number" required placeholder="in days" step="1" min = "1" name="standard_days" className="form-control w-100" value={this.state.standard_days} onChange={this.handleInput} required /></td><td><input type="number" required placeholder="in days" step="1" min = "1" name="premium_days" className="form-control w-100" onChange={this.handleInput} value={this.state.premium_days} required /></td></tr>
                                  {
                                      this.state.features.map((v,i) => {
                                        return <tr key={i}><td><input type="text" placeholder="Add a Feature" required name="feature" className="form-control" value={this.state.features[i].feature} onChange={(e) => this.handleFeature(i, e)} /></td><td><input type="checkbox" name="standard" value="1" checked={this.state.features[i].standard == 0 ? false : true} className="checkbox d-block mx-auto" onChange={(e) => this.handleFeature(i, e)} /></td><td><input type="checkbox" name="premium" value="1" className="checkbox d-block mx-auto" onChange={(e) => this.handleFeature(i, e)} checked readOnly />
                                        {
                                          i > 0 && <span className="removeRow" onClick={() => this.removeFeature(i)}><i className="fa fa-times"></i></span>
                                        }
                                        </td></tr>
                                      })
                                    }
                                </table>
                                <Button type="button" onClick={this.addFeature} size="compact" variant="primary-outline">
                                  <i className="fa fa-plus"></i>
                                </Button>
                              </div>
                      </div>

                      <div className="form-group">
                      <div className="left">
                              <strong>Extras</strong>
                              </div>
                              <div className="right">
                                <table className="table table-bordered">
                                  <tr><td>Fast Delivery</td><td><input type="number" required placeholder="in days" step="1" min = "1" name="fast_days" className="form-control w-100" value={this.state.fast_days} onChange={this.handleInput} /></td><td><span className="input-icon">{this.state.currency}</span> <input type="number" required placeholder="" step="1" min = "1" name="fast_price" className="form-control w-100" value={this.state.fast_price} onChange={this.handleInput} /></td></tr>
                                  {
                                      this.state.extras.map((v,i) => {
                                        return <tr key={i}><td><input type="text" placeholder="Add Extra Feature" required name="feature" className="form-control" onChange={(e) => this.handleExtra(i, e)} value={this.state.extras[i].feature} /></td><td><input type="text" name="value" className="form-control" value={this.state.extras[i].value} onChange={(e)=>this.handleExtra(i, e)} placeholder="value (optional)" /></td><td><span className="input-icon">{this.state.currency}</span> <input type="number" required placeholder="" step="1" min = "1" name="amount" className="form-control w-100" onChange={(e) => this.handleExtra(i, e)} value={this.state.extras[i].amount} />
                                        {
                                          i > 0 && <span className="removeRow" onClick={() => this.removeExtra(i)}><i className="fa fa-times"></i></span>
                                        }
                                        </td></tr>
                                      })
                                    }
                                </table>
                                <Button type="button" onClick={this.addExtra} size="compact" variant="primary-outline">
                                  <i className="fa fa-plus"></i>
                                </Button>
                              </div>
                      </div>

                      <div className="form-group">
                      <div className="left">
                              <strong>Gallery / My Work</strong>
                              </div>
                              <div className="right">
                                <div className={"uploadArea "+this.state.dragclass} onDrop={e => this.handleDrop(e)}
                                  onDragOver={e => this.handleDragOver(e)}
                                  onDragEnter={e => this.handleDragEnter(e)}
                                  onDragLeave={e => this.handleDragLeave(e)}
                                  onClick={e => this.handleClick(e)}
                                  >
                                    <div className="droppedFiles">
                                    {
                                        this.state.files2.map((f, i)=>{
                                          return <div className="file">
                                            <img src={f.content_url !=undefined ? f.content_url : this.state.imgs[i]} />
                                            <i className="fa fa-times" onClick={()=>this.removeFile(i, 2)}></i>
                                          </div>
                                        })
                                      }
                                    {
                                      this.state.files.map((f, i)=>{
                                        return <div className="file">
                                          <img src={this.state.imgs[i]} />
                                          <i className="fa fa-times" onClick={()=>this.removeFile(i)}></i>
                                        </div>
                                      })
                                    }
                                  </div>
                                  Browse / Drag and Drop
                                  
                                  
                                </div>
                                <input type="file" id="gallery" onChange={this.addToGallery} style={{display: "none"}} />
                                {/* <Button type="button" variant="primary" size="compact" >Upload</Button> */}
                              </div>
                      </div>

                      <div className="form-group">
                                <strong>Tags</strong>
                                <MultipleValueTextInput
                                    onItemAdded={(item, allItems) => this.setState({tags: allItems})}
                                    onItemDeleted={(item, allItems) => this.setState({tags: allItems})}
                                    label=""
                                    values={this.state.tags}
                                    name="item-input"
                                    placeholder="Do not include #"
                                />
                                <small className="text-secondary">Separate tags using commas</small>
                            </div>

                      <div className="form-group text-center">
                      <Button type="button" onClick={(e) => this.submit(e, 0)} variant="primary-outline" size="big mr-2" >Save Draft</Button>
                          <Button variant="primary" size="big" >Publish</Button>
                          {/* <Button type="button" variant="primary-outline" className="ml-2" size="big" onClick={(e) => this.review(e)}>Review</Button> */}
                      </div>

                  </form> 
                </div>
             

      )
    }
}

export default AddGigTab;
